.contacts {
  // background: url(../img/bg/map.jpg) no-repeat right center / cover;
  background-image: url(../img/bg/map.jpg);
  background-repeat: no-repeat;
  background-position: right center;
  background-size: contain;
  padding: 80px 0 90px;
  &__inner {
    display: flex;
  }
  &__column {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__title {
    font-size: $fz-title;
    font-weight: 800;
    text-transform: uppercase;
    margin-bottom: 80px;
  }
  &__row {
    font-size: $fz-subtitle;
    margin-bottom: 45px;
  }
  &__tel {
    font-size: $fz-title;
    font-weight: 800;
    text-transform: uppercase;
    &:hover {
      text-decoration: underline;
    }
  }
  &__btn {}
}

@media (min-width: 1200px) {}
@media (min-width: 992px) and (max-width: 1199px) {}
@media (min-width: 768px) and (max-width: 991px) {}
@media (max-width: 767px) { //TODO gradient
  .contacts {
    background-position: center bottom;
    padding: 25px 0 220px;
    &__inner {
      padding: 0 20px;
    }
    &__title {
      font-size: $fz-title-mob;
      margin-bottom: 30px;
    }
    &__btn {
      font-size: $fz-title-mob;
      padding: 20px 45px;
    }
    &__row {
      font-size: $fz-title-mob;
      margin-bottom: 10px;
      &:nth-of-type(even) {
        margin-bottom: 30px;
      }
    }
    &__tel {
      font-size: $fz-default;
    }
  }
}
@media (max-width: 480px) {}
@media (max-width: 320px) {}